import { memo, useCallback, useMemo } from 'react'
import css from './ProductMore.module.scss'
import IllustratedInfo from 'components/Molecules/IllustratedInfo/IllustratedInfo'
import Button, { ButtonVariants } from 'components/Atoms/Button/Button'
import Container from 'components/Templates/Container/Container'
import { useDispatch } from 'react-redux'
import { toggleModal } from 'store/actions'
import ModalDocuments from 'components/Organisms/modals/ModalDocuments/ModalDocuments'

function ProductMore ({
  className,
  heading,
  description,
  buttonLabel,
  docList,
  url,
  image,
  modalImage,
  isFiltered
}) {
  const dispatch = useDispatch()
  
  const handleClickButton = useCallback(() => {
    dispatch(toggleModal(
      <ModalDocuments
        list={docList}
        image={modalImage}
        isFiltered={isFiltered}
      />, {
        isContentOnly: true
      }
    ))
  }, [dispatch, docList, modalImage, isFiltered])
  
  const button = useMemo(() => {
    if (!url && !buttonLabel) return null
    
    if (url) {
      return (
        <Button
          link={url}
          variant={ButtonVariants.FILLED}
          className={css.button}
        >
          {buttonLabel}
        </Button>
      )
    }
    
    return (
      <Button
        onClick={handleClickButton}
        variant={ButtonVariants.FILLED}
        className={css.button}
      >
        { buttonLabel }
      </Button>
    )
  }, [url, docList, buttonLabel, handleClickButton])
  
  return (
    <section className={className}>
      <Container>
        <IllustratedInfo
          className={css.wrapper}
          heading={heading}
          description={description}
          controls={button}
        >
          <img
            src={image}
            className={css.image}
            alt={heading}
          />
        </IllustratedInfo>
      </Container>
    </section>
  )
}

export default memo(ProductMore)