import { memo } from 'react'
import { useSelector } from 'react-redux'
import Product from 'Pages/Product/Product'
import { dataPipes } from 'Pages/Product/Pipes/_assets/data'

function Pipes () {
  const lang = useSelector(store => store.ui.lang)
  const data = dataPipes[lang]
  
  return (
    <Product
      data={data}
    />
  )
}

export default memo(Pipes)