import { memo, useEffect, useState } from 'react'
import { fetchSpecificPost } from 'api/blog'
import { useSelector } from 'react-redux'
import { useLocation, useRouteMatch } from 'react-router-dom/cjs/react-router-dom'

function PostEditDataProvider ({children}) {
  const match = useRouteMatch()
  const location = useLocation()
  const postID = match.params?.id
  const routerData = location.state?.data
  const [postData, setPostData] = useState(routerData)
  const db = useSelector(store => store.firebase.app)
  
  useEffect(() => {
    if (postID && !routerData && !postData) {
      fetchSpecificPost({
        postID,
        db
      })
        .then(response => {
          setPostData({
            ...response,
            image: {
              content: response.imageUrl,
              name: response.heading,
            }
          })
        })
    }
  }, [postData, routerData, postID, db])
  
  return children({
    postData
  })
}

export default memo(PostEditDataProvider)