import { collection, getDocs, getDoc, doc, addDoc, setDoc, deleteDoc } from 'firebase/firestore'
import { getDownloadURL, getStorage, ref, uploadBytes, listAll, deleteObject } from 'firebase/storage'

export function fetchPosts (db) {
  const blogRef = collection(db, `blog`)
  return getDocs(blogRef)
    .then(snapshot => {
      const data = []
      snapshot.forEach(doc => {
        const post = doc.data()
        
        const imageRequest = post?.image
          ? fetchImageURL({
            postID: doc.id,
            fileName: post.image
          })
            .then(url => {
              return {
                ...post,
                id: doc.id,
                imageUrl: url,
                createdAt: post.createdAt?.seconds
                  ? post.createdAt.seconds * 1000
                  : post.createdAt
              }
            })
          : new Promise(resolve => resolve(post))
        
        data.push(imageRequest)
      })
      
      return Promise.all(data)
    })
}

export function fetchSpecificPost ({postID, db}) {
  const postRef = doc(db, `/blog/${postID}`)
  
  return getDoc(postRef)
    .then(snapshot => {
      const data = snapshot.data()
      return {
        ...data,
        id: snapshot.id
      }
    })
    .then(postData => {
      return fetchImageURL({postID: postData.id, fileName: postData.image})
        .then(url => ({
          ...postData,
          createdAt: postData.createdAt?.seconds
            ? postData.createdAt.seconds
            : postData.createdAt,
          imageUrl: url
        }))
    })
}

export function fetchImageURL ({postID, fileName}) {
  const storage = getStorage()
  const imageRef = ref(storage, `blog/${postID}/${fileName}`)
  
  return getDownloadURL(imageRef)
    .catch(() => {
      console.log(`smth wrong`)
    })
}

export function saveImage ({postID, image}) {
  const storage = getStorage()
  const storageRef = ref(storage, `blog/${postID}/${image.name}`)
  
  return uploadBytes(storageRef, image)
}

export function savePost ({db, data}) {
  const collectionRef = collection(db, `blog`)
  
  return addDoc(collectionRef, data)
    .then(response => {
      return response.id
    })
}

export function updatePost ({db, data}) {
  const {
    id,
    ...postData
  } = data
  const collectionRef = collection(db, `blog`)
  const postRef = doc(collectionRef, id)
  
  return setDoc(postRef, postData)
}

export function deleteAllImages (id) {
  const storage = getStorage()
  const storageRef = ref(storage, `blog/${id}`)
  
  return listAll(storageRef)
    .then(data => {
      data.items.forEach(fileRef => {
        return deleteObject(fileRef)
      })
    })
}

export function deleteBlogPost ({db, id}) {
  const postRef = doc(db, `blog/${id}`)
  
  return deleteDoc(postRef)
    .then(() => {
      return deleteAllImages(id)
    })
}