import { memo } from 'react'
import { useSelector } from 'react-redux'
import Product from 'Pages/Product/Product'
import { dataTurbines } from 'Pages/Product/Turbines/_assets/data'

function Turbines () {
  const lang = useSelector(store => store.ui.lang)
  const data = dataTurbines[lang]
  
  return (
    <Product
      data={data}
    />
  )
}

export default memo(Turbines)