import css from './BlogPost.module.scss'
import Container from 'components/Templates/Container/Container'
import Button, { ButtonVariants } from 'components/Atoms/Button/Button'
import { BLOG, BLOG_POST_EDIT } from 'utils/routes'
import Heading, { HeadingTypes } from 'components/Atoms/Heading/Heading'
import { isMobile } from 'react-device-detect'
import { useCallback } from 'react'
import PostDataProvider from 'components/Organisms/PostDataProvider/PostDataProvider'
import PreloaderManager from 'components/Organisms/PreloaderManager/PreloaderManager'
import { LoadingStatuses } from 'utils/const'
import { useHistory } from 'react-router-dom/cjs/react-router-dom'

function BlogPost (props) {
  const history = useHistory()
  
  const handleClickEdit = useCallback(({id, image, imageUrl, ...data}) => {
    console.log(data)
    history.push({
      pathname: `${BLOG_POST_EDIT}/${id}`,
      state: {
        data: {
          id,
          image: {
            name: image,
            content: imageUrl
          },
          ...data
        }
      }
    })
  }, [history])
  
  const renderContent = useCallback((({id, imageUrl, image, heading, content, dateString, isAuth, fetchingStatus, preview, createdAt} = {}) => {
    return (
      <section className={css.section}>
        <PreloaderManager
          isDataExists={Boolean(content)}
          isLoading={fetchingStatus === LoadingStatuses.LOADING}
          isLoaded={fetchingStatus === LoadingStatuses.LOADED}
        >
          <Container className={css.wrapper}>
            {!isMobile &&
              <Button
                className={ css.backlink }
                variant={ ButtonVariants.ARROW_REVERSE }
                link={ BLOG }
              >
                Назад
              </Button>
            }
            <article className={css.content}>
              {isMobile &&
                <Button
                  className={ css.backlink }
                  variant={ ButtonVariants.ARROW_REVERSE }
                  link={ BLOG }
                >
                  Назад
                </Button>
              }
              <header className={css.header}>
                <Heading
                  className={css.heading}
                  tag={HeadingTypes.H2}
                  style={HeadingTypes.H4}
                >
                  { heading }
                </Heading>
                <span className={css.date}>
                {dateString}
              </span>
              </header>
              <div
                dangerouslySetInnerHTML={{ __html: content }}
                className={css.wyziwig}
              />
              { isAuth &&
                <Button
                  onClick={ () => handleClickEdit({id, heading, content, preview, createdAt, image, imageUrl}) }
                  className={ css.button }
                  variant={ ButtonVariants.EDIT }
                >
                  Редактировать
                </Button>
              }
            </article>
            {imageUrl && (
              <div className={css.frame}>
                <img
                  src={imageUrl}
                  className={css.image}
                  alt={heading}
                />
              </div>
            )}
          </Container>
        </PreloaderManager>
      </section>
    )
  }), [handleClickEdit])
  
  
  return (
    <PostDataProvider
      renderChildren={renderContent}
      {...props}
    />
  )
}

export default BlogPost