import css from './Products.module.scss'
import {useSelector} from 'react-redux'
import BannerProducts from 'components/Molecules/BannerProducts/BannerProducts'
import { productsData } from 'Pages/Products/_assets/data'
import ProjectsList from 'components/Molecules/ProjectsList/ProjectsList'
import { dataProducts } from 'utils/dataProducts'
import useScrollIntoView from 'hooks/useScrollIntoView'
import Tags from 'components/Organisms/Tags/Tags'
import Container from 'components/Templates/Container/Container'

function Products () {
  const lang = useSelector(store => store.ui.lang)
  
  const {
    addItemToObserver,
    list
  } = useScrollIntoView()
  
  return (
    <section>
      <BannerProducts {...dataProducts[lang]} />
      <Container>
        <Tags
          className={css.tags}
          list={list}
        />
      </Container>
      <ProjectsList
        className={css.list}
        data={productsData}
        getItemRef={addItemToObserver}
      />
    </section>
  )
}

export default Products