import React from 'react'
import { Route, Switch } from 'react-router-dom'
import Home from 'Pages/Home/Home'
import Blog from 'Pages/Blog/Blog'
import Projects from 'Pages/Projects/Projects'
import {
  BLOG,
  BLOG_POST_CREATE,
  BLOG_POST_EDIT,
  HOME_PAGE,
  PRODUCTS,
  PROJECTS
} from 'utils/routes'
import Products from './Products/Products'
import BlogPost from 'Pages/BlogPost/BlogPost'
import CreatePost from 'Pages/CreatePost/CreatePost'
import Page404 from 'Pages/Page404/Page404'
import PostEditDataProvider from 'components/Organisms/PostEditDataProvider/PostEditDataProvider'
import Trafos from 'Pages/Product/Trafos/Trafos'
import HV from 'Pages/Product/HV/HV'
import Pipes from 'Pages/Product/Pipes/Pipes'
import Turbines from 'Pages/Product/Turbines/Turbines'

// TODO apply async import of page components to split the initial js chunk
const Routes = ({isAuth}) => {
  return (
    <Switch>
      <Route exact path={HOME_PAGE} component={Home} />
      <Route exact path={PROJECTS} component={Projects} />
      <Route exact path={PRODUCTS} component={Products} />
      <Route exact path={`${PRODUCTS}/trafo`} component={Trafos} />
      <Route exact path={`${PRODUCTS}/ais`} component={HV} />
      <Route exact path={`${PRODUCTS}/pipes`} component={Pipes} />
      <Route exact path={`${PRODUCTS}/turbines`} component={Turbines} />
      <Route exact path={BLOG} component={Blog} />
      <Route exact path={`${BLOG}/:id`} component={BlogPost} />
      { isAuth &&
        <Switch>
          <Route exact path={BLOG_POST_CREATE} component={CreatePost} />
          <Route exact path={`${BLOG_POST_EDIT}/:id`}>
            <PostEditDataProvider>
              {({postData}) => (
                <CreatePost data={postData} isEditing />
              )}
            </PostEditDataProvider>
          </Route>
        </Switch>
      }
      <Route component={Page404} />
    </Switch>
  )
}

export default Routes
