import { memo, useCallback, useState } from 'react'
import css from './CreatePost.module.scss'
import Container from 'components/Templates/Container/Container'
import Heading from 'components/Atoms/Heading/Heading'
import Button, { ButtonVariants } from 'components/Atoms/Button/Button'
import { BLOG } from 'utils/routes'
import { FormItemTypes } from 'utils/const'
import Form from 'components/Organisms/Form/Form'
import { useDispatch, useSelector } from 'react-redux'
import { deleteAllImages, deleteBlogPost, saveImage, savePost, updatePost } from 'api/blog'
import { stateToHTML } from 'draft-js-export-html'
import { useHistory } from 'react-router-dom/cjs/react-router-dom'
import { toggleModal } from 'store/actions'
import ModalMessage from 'components/Organisms/modals/ModalMessage/ModalMessage'

function CreatePost ({data, isEditing}) {
  const FORM_NAMESPACE = `create-post`
  
  const [isSubmitting, setSubmitting] = useState(false)
  const db = useSelector(store => store.firebase.app)
  const history = useHistory()
  const dispatch = useDispatch()
  
  const {
    id: postID,
    createdAt
  } = data ?? {}
  
  const submitForm = useCallback(data => {
    const {
      [`${FORM_NAMESPACE}-heading`]: heading,
      [`${FORM_NAMESPACE}-content`]: content,
      [`${FORM_NAMESPACE}-image`]: image,
      [`${FORM_NAMESPACE}-preview`]: preview,
    } = data
    
    const dataToSubmit = {
      heading,
      preview,
      content: stateToHTML(content.getCurrentContent()),
      createdAt: isEditing && createdAt ? createdAt : Date.now(),
      image: image[0].name
    }
    
    setSubmitting(true)
    
    if (isEditing) {
      updatePost({db, data: {id: postID, ...dataToSubmit}})
        .then(() => {
          if (image[0]?.file) {
            return deleteAllImages(postID)
              .then(() => {
                return saveImage({postID, image: image[0].file})
              })
          }
        })
        .then(() => {
          setSubmitting(false)
          history.push({
            pathname: BLOG
          })
        })
    } else {
      savePost({db, data: dataToSubmit})
        .then(postID => {
          return saveImage({postID, image: image[0].file})
        })
        .then(response => {
          setSubmitting(false)
          history.push({
            pathname: BLOG
          })
        })
    }
  }, [db, isEditing, postID, history, FORM_NAMESPACE, createdAt])
  
  const deletePost = useCallback(() => {
    setSubmitting(true)
  
    deleteBlogPost({
      db,
      id: postID
    })
      .then(() => {
        setSubmitting(false)
        dispatch(toggleModal(null))
        history.push({
          pathname: BLOG
        })
      })
  }, [postID, db, history, dispatch])
  
  const handleClickDelete = useCallback(() => {
    dispatch(toggleModal((
      <ModalMessage
        heading={`Вы уверены, что хотите удалить пост?`}
        description={`В случае подтверждения операции все данные, связанные с текущим постом, будут безвозвратно удалены.`}
        submitButtonHandler={deletePost}
        isLoading={isSubmitting}
      />
    )))
  }, [dispatch, isSubmitting, deletePost])
  
  const cancelPostEdit = useCallback(() => {
    dispatch(toggleModal(null))
    
    history.push({
      pathname: BLOG
    })
  }, [history, dispatch])
  
  const handleClickCancel = useCallback(() => {
    dispatch(toggleModal((
      <ModalMessage
        heading={`Вы уверены, что хотите покинуть страницу редактирования поста?`}
        description={`В случае подтверждения все все сделанные вами изменения будут безвозвратно удалены.`}
        submitButtonHandler={cancelPostEdit}
      />
    )))
  }, [dispatch, cancelPostEdit])
  
  if (isEditing && !data) return null
  
  const headingLabel = isEditing
    ? `Редактировать запись`
    : `Создать запись`
  
  const {
    id,
    content: postContent,
    image: defaultImage,
    heading,
    preview
  } = data || {}
  
  const backlinkURL = isEditing
    ? `${BLOG}/${id}`
    : BLOG
  
  const formDescription = [
    {
      element: FormItemTypes.INPUT,
      name: `${FORM_NAMESPACE}-heading`,
      defaultValue: heading,
      validation: {
        required: true
      },
      placeholder: `Введите заголовок поста`,
      className: css.block
    },
    {
      element: FormItemTypes.INPUT,
      name: `${FORM_NAMESPACE}-preview`,
      defaultValue: preview,
      validation: {
        required: true
      },
      placeholder: `Введите краткое описание темы поста`,
      className: css.block
    },
    {
      element: FormItemTypes.EDITOR,
      name: `${FORM_NAMESPACE}-content`,
      defaultValue: postContent,
      validation: {
        required: true
      },
      className: css.block
    },
    {
      element: FormItemTypes.IMAGE,
      callbackOnChange: () => {},
      defaultValue: defaultImage ? [defaultImage] : null,
      label: `Прикрепить файл`,
      name: `${FORM_NAMESPACE}-image`,
      isMultiple: false,
      className: css.imageInput,
      validation: {
        required: `Пожалуйста, приложите иллюстрацию к посту`
      }
    }
  ]
  
  return (
    <section className={css.section}>
      <Container>
        <div className={css.header}>
          <Heading>
            { headingLabel }
          </Heading>
          <Button
            variant={ButtonVariants.ARROW_REVERSE}
            link={backlinkURL}
          >
            Назад
          </Button>
        </div>
        <Form
          submitForm={submitForm}
          data={formDescription}
        >
          {({items}) => (
            <>
              { items }
              <div className={css.footer}>
                {isEditing && (
                  <Button
                    onClick={handleClickDelete}
                    isLoading={isSubmitting}
                    variant={ButtonVariants.BORDERED}
                    type="button"
                  >
                    Удалить пост
                  </Button>
                )}
                <Button
                  onClick={handleClickCancel}
                  isLoading={isSubmitting}
                  className={css.buttonCancel}
                  variant={ButtonVariants.BORDERED}
                  type="button"
                >
                  Отменить
                </Button>
                <Button
                  isLoading={isSubmitting}
                  className={css.buttonSubmit}
                  variant={ButtonVariants.FILLED}
                  type="submit"
                >
                  Поделиться
                </Button>
              </div>
            </>
          )}
        </Form>
      </Container>
    </section>
  )
}

export default memo(CreatePost)