import { memo, useMemo } from 'react'
import css from './ProjectCard.module.scss'
import classnames from 'classnames'
import Heading, {HeadingTypes} from 'components/Atoms/Heading/Heading'
import IconTimeline from 'assets/icons/IconTimeline'
import IconLocationFancy from 'assets/icons/IconLocationFancy'
import Link from 'components/Atoms/Link/Link'
import IconArrowButton from 'assets/icons/IconArrowButton'

function ProjectCard ({
  className,
  image,
  name,
  tag,
  country,
  region,
  duration,
  type,
  customer,
  description,
  isOdd,
  TagName = `li`,
  detailsUrl,
  getItemRef
}) {
  const details = useMemo(() => {
    const isDetailsRequired = country || region || duration
    
    if (!isDetailsRequired) return null
    
    return (
      <ul className={css.details}>
        {country && (
          <li className={css.detail}>
            <IconLocationFancy className={classnames(css.icon, css.iconLocation)}/>
            {country}
          </li>
        )}
        {region && (
          <li className={css.detail}>
            <IconLocationFancy className={classnames(css.icon, css.iconLocation)}/>
            {region}
          </li>)
        }
        {duration && (
          <li className={css.detail}>
            <IconTimeline className={classnames(css.icon, css.iconTimeline)}/>
            {`${duration.start}-${duration.end}`}
          </li>
        )}
      </ul>
    )
    
  }, [country, region, duration])
  
  const list = useMemo(() => {
    const isListRequired = type || customer
    
    if (!isListRequired) return null
    
    return (
      <dl className={css.list}>
        {type && (
          <div className={css.item}>
            <dt className={css.key}>
              Вид работ
            </dt>
            <dd className={css.value}>
              {type}
            </dd>
          </div>
        )}
        {customer && (
          <div className={css.item}>
            <dt className={css.key}>
              Заказчик
            </dt>
            <dd className={css.value}>
              {customer}
            </dd>
          </div>
        )}
      </dl>
    )
  }, [type, customer])
  
  return (
    <TagName
      className={classnames(css.wrapper, className, {
        [css.wrapperOdd]: isOdd
      })}
      ref={node => {
        getItemRef && getItemRef({
          node,
          label: tag
        })
      }}
    >
      {image && (
        <img
          className={css.img}
          src={image}
          alt={name}
        />
      )}
      <div className={css.description}>
        <Heading
          tag={HeadingTypes.H4}
          style={HeadingTypes.H4}
          isLightest={isOdd}
        >
          { name }
        </Heading>
        {details}
        {list}
        <div className={css.wyziwig} dangerouslySetInnerHTML={{ __html: description }} />
        {detailsUrl && (
          <Link
            className={css.link}
            link={detailsUrl}
          >
            Подробнее
            <IconArrowButton className={css.iconArrow} />
          </Link>
        )}
      </div>
    </TagName>
  )
}

export default memo(ProjectCard)