import { memo, useCallback, useMemo, useState } from 'react'
import css from './ModalDocuments.module.scss'
import Link from 'components/Atoms/Link/Link'
import IconPDF from 'assets/icons/IconPDF'
import IconDoc from 'assets/icons/IconDoc'
import Input, { InputVariants } from 'components/Atoms/Input/Input'

function ModalDocuments ({
  list,
  image,
  isFiltered
}) {
  const [filter, setFilter] = useState(``)
  
  const getIcon = useCallback(url => {
    if (url.toUpperCase().includes(`.PDF`)) {
      return IconPDF
    }
    
    if (url.toUpperCase().includes(`.DOC`)) {
      return IconDoc
    }
  }, [])
  
  const items = useMemo(() => {
    const processedList = isFiltered
      ? list.filter(({label}) => label.toUpperCase().includes(filter.toUpperCase()))
      : list
    
    return processedList.map(({url, label}) => {
      const Icon = getIcon(url)
      
      return (
        <li className={css.item} key={url}>
          <Link
            className={css.link}
            link={url}
          >
            <Icon className={css.icon}/>
            {label}
          </Link>
        </li>
      )
    })
  }, [isFiltered, filter, getIcon, list])
  
  return (
    <div
      className={css.wrapper}
    >
      <img
        src={image}
        className={css.image}
        alt={`Smth`}
      />
      <div>
        {isFiltered && (
          <Input
            value={filter}
            onChange={evt => setFilter(evt.target.value)}
            variant={InputVariants.SEARCH}
            placeholder={ `Введите поисковый запрос` }
            className={css.filter}
          />
        )}
        <ul className={css.list}>
          {items}
        </ul>
      </div>
    </div>
  )
}

export default memo(ModalDocuments)