import { memo, useEffect, useRef } from 'react'
import css from './Product.module.scss'
import ProductDescription from 'components/Molecules/ProductDescription/ProductDescription'
import useIntersectionObserver from 'hooks/useIntersectionObserver'
import Container from 'components/Templates/Container/Container'
import SliderSuppliers from 'components/Organisms/SliderSuppliers/SliderSuppliers'
import RibbonProduct from 'components/Molecules/RibbonProduct/RibbonProduct'
import ProductMore from 'components/Molecules/ProductMore/ProductMore'
import SliderPreviews from 'components/Organisms/SliderPreviews/SliderPreviews'
import { isMobile } from 'react-device-detect'

function Product ({data}) {
  const wrapperRef = useRef(null)
  
  const {
    isVisible,
    unsubscribeObserver
  } = useIntersectionObserver({
    elementRef: wrapperRef,
    margins: isMobile ? "0% 0% 0% 0%" : "20% 0% 20% 0%"
  })
  
  useEffect(() => {
    if (isVisible) {
      unsubscribeObserver()
    }
  }, [unsubscribeObserver, isVisible])
  
  return (
    <section className={css.wrapper}>
      <ProductDescription
        className={css.description}
        elementRef={wrapperRef}
        isVisible={isVisible}
        {...data.description}
      />
      <Container className={css.slider}>
        <SliderSuppliers
          data={data.partners}
        />
      </Container>
      <RibbonProduct
        className={css.ribbon}
        {...data.timeline}
      />
      <ProductMore
        {...data.productMore}
      />
      {data?.feedback && (
        <Container>
          <SliderPreviews
            sliderProps={{
              spaceBetween: isMobile ? 20 : 60
            }}
            heading={data.feedback.heading}
            list={data.feedback.list.sort((a, b) => b.year - a.year)}
          />
        </Container>
      )}
    </section>
  )
}

export default memo(Product)