import { memo } from 'react'
import { useSelector } from 'react-redux'
import { dataTrafos } from 'utils/dataProduct'
import Product from 'Pages/Product/Product'

function Trafos () {
  const lang = useSelector(store => store.ui.lang)
  const data = dataTrafos[lang]
  
  return (
    <Product
      data={data}
    />
  )
}

export default memo(Trafos)