import { memo, useCallback, useMemo } from 'react'
import css from './Tags.module.scss'
import classnames from 'classnames'

function Tags ({
  className,
  list
}) {
  const handleClickButton = useCallback(node => {
    node.scrollIntoView({
      behavior: `smooth`
    })
  }, [])
  
  const items = useMemo(() => {
    return list.map(({node, label}, index) => (
      <li className={css.item} key={index}>
        <button
          onClick={() => handleClickButton(node)}
          className={css.button}
          type="button"
        >
          {`#${label}`}
        </button>
      </li>
    ))
  }, [list, handleClickButton])
  
  return (
    <ul className={classnames(css.list, className)}>
      {items}
    </ul>
  )
}

export default memo(Tags)